import React from 'react';
import PromiseRender from './PromiseRender';
import { CURRENTAUTH } from './index';

function isPromise(obj) {
	return !!obj && (typeof obj === 'object' || typeof obj === 'function' && typeof obj.then === 'function')
}

/**
 * 
 * Common check permissions method
 * @param {  Permission judgment type string |array | Promise | Function } authority
 * @param {  Your permission description  type:string} currentAuthority
 * @param {  Passing components } target
 * @param {  no pass components } Exception
 */

 const checkPermissions = (authority, currentAuthority, target, Exception) => {
 	// authority.
 	if (!authority) {
	    return target;
	}
	// authority
  if (Array.isArray(authority)) {
    if (authority.indexOf(currentAuthority) >= 0) {
      return target;
    }
    return Exception;
  }
  // authoritystring
  if (typeof authority === 'string') {
	  if (authority === currentAuthority) {
	    return target;
	  }
	  return Exception;
  }
  // authorityPromise
  if (isPromise(authority)) {
    return () => (
      <PromiseRender ok={target} error={Exception} promise={authority} />
    );
  }
  // authorityFunction,Function
  if (typeof authority === 'function') {
  	try {
  		const bool = authority();
  		if (bool) {
  			return target;
  		}
  		return Exception;
  	} catch (error) {
  		throw error;
  	}
  }
  throw new Error('')
 }

const check = (authority, target, Exception) => {
 	return checkPermissions(authority, CURRENTAUTH, target, Exception)
 }

 export default check;
 export { checkPermissions };



