import React, { Component } from 'react';
import { PropTypes } from 'prop-types';
import { Modal, Title, Grid, Divider, Icon, Tooltip } from 'components';
import Indicator from 'component/indicator';
import services from 'api/services';
import urls from 'api/urls';

import styles from './index.less';

const Row = Grid.Row;
const Col = Grid.Col;

export default class SeatInfoModal extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: {}
    }
  }

  static propTypes = {
    umId: PropTypes.string,
    visible: PropTypes.bool
  }

  componentWillReceiveProps (nextProps) {
    if (this.props.umId && (nextProps.umId === this.props.umId)) {
      return false;
    } else {
      this.setState({
        umId: nextProps.umId
      }, () => {
        this.getData(nextProps.umId)
      })
    }
  }

  handleUpdateState = (data) => {
    this.setState({
      data: data
    })
  }

  getData = (umId) => {
    if (umId) {
      // console.log('umId2', umId);
      services.get(urls.querySeatIndex, {umId: umId}, this.handleUpdateState)
    }
  }

  handleCancel = () => {
      const { umId, handleModalStatus } = this.props
      if (typeof handleModalStatus === 'function') {
          const visible = false;
          handleModalStatus(umId, visible)
      }
  }

  render() {
    const { visible } = this.props;
    const { data } = this.state;

    return (
      <Modal visible={visible} title='' footer={null} onCancel={this.handleCancel} width={800} >
        <Row className={styles.row} >
          <Title title='' style={{paddingLeft: '0'}} />
        </Row>
        <Row className={styles.row}>
          <Col span={6} className={styles.col}>
            <Indicator title='' data={data.tmrName} />
          </Col>
          <Col span={6} className={styles.col}>
            <Indicator title='' data={data.onboardAge} />
          </Col>
          <Col span={6} className={styles.col}>
            <Indicator title='' data={data.tmrType} />
          </Col>
          <Col span={6} >
            <Indicator title='' data={data.positionName} />
          </Col>
        </Row>
        <Divider className={styles.divider} />
        <Row className={styles.row}>
          <Title title='' style={{paddingLeft: '0'}} />
        </Row>
        <Row className={styles.row}>
          <Col span={8}>
            <Indicator title='' data={data.isActive} />
          </Col>
        </Row>
        <Divider className={styles.divider} />
        <Row className={styles.row}>
          <Title title='' style={{paddingLeft: '0'}} />
        </Row>
        <Row className={styles.row}>
          <Col span={8} className={styles.col}>
            <Indicator title='' data={data.todayTotalCiPreminm} />
          </Col>
          <Col span={8} className={styles.col}>
            <Indicator title='' data={data.securityThroughCustCnt} width='70%' />
          </Col>
          <Col span={8}>
            <Indicator title='' data={data.todayTotalNciPremium} />
          </Col>
        </Row>
        <Divider className={styles.divider} />
        <Row className={styles.row}>
          <Title title='' style={{paddingLeft: '0'}} />
        </Row>
        {/* <Row className={styles.row}>
          <Col span={8} className={styles.col}>
            <Indicator title='' data={data.name} />
          </Col>
          <Col span={8} className={styles.col}>
            <Indicator title='' data={data.name} />
          </Col>
          <Col span={8}>
            <Indicator title='' data={data.name} />
          </Col>
        </Row> */}
        <Row className={styles.row}>
          <Col span={8} className={styles.col}>
            <Indicator title='()' data={data.refaltimeEffecTalkTime} width='75%' />
          </Col>
          <Col span={8} className={styles.col}>
            <Indicator title='()' data={data.refaltimeEffecTalkNum} width='75%' />
          </Col>
          <Col span={8}>
            <Indicator title='()' data={data.refaltimeAvgTalkTime} width='75%' />
          </Col>
        </Row>
        <Row className={styles.row}>
          <Col span={8} className={styles.col}>
            <Indicator title='' data={data.todayTotalTalkTime} />
            <Tooltip placement='right' title={` ${data.lastSumTime}`}>
              <Icon type={data.todayTotalTalkTime > data.lastSumTime ? 'arrowup' : 'arrowdown'}
                className={styles.fixIcon}
              />
            </Tooltip>
          </Col>
          <Col span={8} className={styles.col}>
            <Indicator title='' data={data.todayTotalTalkNum} />
            <Tooltip placement='right' title={` ${data.lastSumCount}`}>
              <Icon type={data.todayTotalTalkNum >= data.lastSumCount ? 'arrowup' : 'arrowdown'} className={styles.fixIcon} />
            </Tooltip>
          </Col>
          <Col span={8}>
            <Indicator title='' data={data.todayTotalAvgTalkTime} />
            <Tooltip placement='right' title={` ${data.lastSumAvg}`}>
              <Icon type={data.todayTotalAvgTalkTime > data.lastSumAvg ? 'arrowup' : 'arrowdown'} className={styles.fixIcon} style={{ right: '8px' }} />
            </Tooltip>
          </Col>
        </Row>
      </Modal>
    )
  }
}



